
.main_container2{
    max-width:1000px;
    margin: 60px auto;
    display: flex;
    justify-content: space-between;
}

.photo {
    width: 33%;
    display: flex;
    
}
.photo img{
    width:100%;
    border-radius: 6px;

}
.though {
    width: 64%;
    text-align: left;
    
    
}
.though u {
    color: #51c8de;
    font-weight: bold;
}

.though h1 {
    font-size: xxx-large;
}
.though p {
    color: rgb(97, 96, 96);
}

@media only screen and (max-width: 750px){
    .main_container2 {
        flex-direction: column;
        width: 100%;
    }
    .photo {
        width: 100%;
        padding: 0px 20px 0px 20px;
    }
    .though {
        width: 100%;
        padding: 15px 20px 0px 20px;
    }
    .though u {
        font-size: 15px;
    }
    .though h1 {
        font-size: 20px;
        padding-top: 7px;
    }
    .though p {
        font-size: 13px;
        padding-top: 5px;
    }

}