.info {
    background-color: #1d2a4d;

}
.touch u {
    text-align: left;
}
.overall {
    display: flex;
    flex-wrap: wrap;
    padding-top: 50px;
    padding-bottom: 50px;
}

.footer_item {
    width: 25%;
}


.button ul li {
    list-style: none;
    padding: 1%;
    justify-content: right;    
}

.button ul li span p {
    display: inline-block;
    font-size: small;
    margin: 1px 6px;
}

.coloum {
    display: flex;
    flex-wrap: wrap;
    border-radius: 5px;
}

.footer_item u {
    color: #15b0c9;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
}

.footer_item p {
    color: white;
}
.follo {
    padding-top: 35px;
}
.logo {
    display: flex;
    flex-wrap: wrap;
    padding-top: 13px;
}
.logo img {
    width: 35px;
    height: auto;
    margin: 0px 5px;
    background-color: #fff;
    border-radius: 30px;
}

.service {
    padding: 0px 0px 70px;
}


.headindtext {
    text-align: center;
}

.emailid {border-radius: 5px;}

.emailid input {
    background-color: white;
    border: none;
    padding: 10px;
    border-radius: 5px 0px 0px 5px;
}

.signup input {
    background-color: #15b0c9;
    border: none;
    padding: 10px 10px;
    border-radius: 0px 5px 5px 0px;
}

.follow {
    padding-top: 10px;
}

.follow h5 {
    color: #169dbc;
    font-size: 14px;
}

.links ul {
    margin: 0px;
    padding: 0px;
    list-style: none;
}

.links ul li {
    color: white;
    position: relative;
    padding-left: 15px;
}
.links ul li a {
    text-decoration: none;
    color: white;
}
.links ul li:after {
    content: "";
    height: 7px;
    width: 7px;
    border: 1px solid white;
    position: absolute;
    left: 0px;
    transform: rotate(45deg);
    border-left: 0px;
    border-bottom: 0px;
    top: 8px;
}

.button ul {
    padding-top: 10px;
}


.button ul li span {
    display: flex;
    align-items: center;
}

.button ul li span img {
    margin-right: 5px;
    max-width: 20px;
    width: 100%;
    display: block;
}

.button ul li span p {
    margin: 0px;
    /* font-size: 13px; */
}

@media only screen and (max-width: 750px){ 
    .footer_item {
        width: 50%;
    }
    
    .overall {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        
    }
    .footer_item.touch {
        width: 100%;
        padding: 0px 20px 0px 20px;
        
    }
    .touch u {
        font-size: 15px;
    }
    .touch p {
        font-size: 13px;
    }
    .links {
        width: 100%;
        padding: 20px 20px 0px 20px;
    }
    .links u {
        font-size: 15px;
    }
    .links ul {
        font-size: 13px;
    }
    .letter {
        width: 100%;
        padding: 20px 20px 0px 20px ;
    }
    .letter u {
        font-size: 15px;
        padding-bottom: 12px;
    }
}