.allpart {
    display: flex;
    flex-wrap: wrap;
}
.gtext {
    text-align: center;
}

.gtext u {
    color: #51c8de;
    font-weight: bold;
}

.galleryimage {
    width: 33.33%;
    padding: 10px;
}

.gimage img {
    max-width: 100%;
    border-radius: 4%;
}

@media only screen and (max-width: 567px){ 
    .allpart {
        width: 100%;
        flex-direction: column;
    }
    .gtext {
        width: 100%;
        padding: 0px 20px 0px 20px;
    }
    .gtext u {
        font-size: 15px;
    }
    .gtext h1 {
        font-size: 20px;
    }
    .galleryimage {
        width: 100%;
        padding: 2px 20px 2pxpx 20px;
    }
}
