

.headingtext {
    text-align: center;
}
.service u {
    color: #51c8de;
    font-weight: bold;
}
.headingtext h1 {
    font-weight: bold;
}

/* .iconbutton ul {
    display: flex;
    flex-wrap: wrap;
} */

.iconbutton {
    display: flex;
    flex-wrap: wrap;
}

.itemsname {
    width: 33.33%;
    padding: 10px;
}

.items {
    background-color: #eff5f9;
    border-radius: 2px;
    height: 100%;
    padding: 40px;
}

.icon {
    border-radius: 56%;
    background-color: #13c5dd;
    height: 100px;
    width: 100px;
    margin: auto;
    padding: 25px 0px;
    text-align: center;
}
.icon img {
    height: 50px;
}
.items h3 {
    text-align: center;
}
.items p {
    text-align: center;
}

@media only screen and (max-width: 750px){
    .iconbutton {
        flex-direction: column;
        justify-content: center;
        width: 100%;
    }
    .headingtext {
        width: 100%;
    }
    .itemsname {
        width: 100%;    
    }
    .items {
        width: 100%;
    }
    .service u {
        font-size: 15px;
    }
    .service h1 {
        font-size: 20px;
    }
    .items h3 {
        font-size: 20px;
    }
    .items p {
        font-size: 13px;
    }

}
