.main_container3 {
    max-width: 1000px;
    margin: 0px auto;
    display: flex;
    justify-content: space-between;
    padding: 100px 0px;
}

.appoint {
    background-color: #13c5dd;
}
.appointment {
    display: flex;
}

.appoint_text {
    text-align: left;
    padding: 10px;
    width: 40%;   
}
.appoint_text u {
    font-weight: bold;
    color: aliceblue;
}

.find {
    padding: 20px 0px;
    
}

.find button {
    padding: 11px 22px;
    border-radius: 20px;
    font-weight: 600;
    
}
.find button:nth-child(1) {
    margin-right: 30px;
    background-color: cadetblue;
}
.logo img {
    width: 35px;
    height: auto;
    margin: 0px 5px;
    background-color: #fff;
    border-radius: 30px;
}

.rightappoint {
    box-sizing: border-box;
    border-color: aliceblue;
    width: 55%;
    background-color: white;
    border-radius: 10px;
    padding: 30px;
}

.headindtext {
    text-align: center;
}

.book {
    text-align: center;
    padding-bottom: 35px;
}

.chooseit {
    display: flex;
    flex-wrap: wrap;
}

.chooseit form {
    width: 50%;
}
.service {
    padding: 0px 0px 70px;
}
.input-item {
    width: 50%;
    padding: 0px 10px;
    margin-bottom: 15px;
}

.input-item input {
    width: 100%;
    padding: 12px 10px;
    border: 1px solid #e6e7e8;
    border-radius: 3px;
    background-color: aliceblue;
}

.chooseit form input {
    border-radius: 7px;
    /* justify-content: center; */
    padding: 5px;
    margin: 0px 55px;
}
.gallery {
    padding: 70px 0px;
}

.input-item {
    width: 50%;
    padding: 0px 10px;
    margin-bottom: 15px;
}

.input-item input {
    width: 100%;
    padding: 12px 10px;
    border: 1px solid #e6e7e8;
    border-radius: 3px;
    background-color: aliceblue;
}

.input-button {
    width: 100%;
    text-align: center;
    padding: 0px 10px 20px;
}

.input-button button {
    display: inline-block;
    border: 1px solid #13c5dd;
    background-color: #13c5dd;
    border-radius: 5px;
    width: 100%;
    padding: 10px 10px;
    color: white;
}

@media only screen and (max-width: 750px){ 
    .main_container3 {
        width: 100%;
        flex-direction: column;
        padding-top: 20px;
    }
    .appoint_text {
        width: 100%;
        padding: 0px 20px 0px 20px;
    }
    .rightappoint {
        width: 100%;
        padding: 0px 20px 0px 20px;
    }
    .chooseit {
        flex-direction: column;
    }
    
    .input-item {
        width: 100%;
    }
    .find button {
        font-size: 10px;
    }
    .find button:nth-child(1) {
        margin: 0px 25px 0px 30px;
    }
    .appoint_text u {
        font-size: 15px;
    }
    .appoint_text h1 {
        font-size: 20px;
    }
    .appoint_text p {
        font-size: 13px;
    }
    .book h1 {
        font-size: 22px;
        padding-top: 20px;
    }

}
