* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  display: flex;
}

.main_container{
  max-width:1000px;
  margin: 0px auto;
}

.logoleft {
  color: #46d3e6;
  width: 30%;
  text-align: -webkit-center;
  padding: 41px;
  font-size: 30px;
}

.menuright {
  width: 65%;
  list-style: none;
}

.menuright ul {
  list-style: none;
  padding: 30px 0px;
  justify-content: right;
  display: flex;
}

.menuright ul li {
  list-style: none;
  padding: 10px;
}
.menuright ul li a {
  color: black;
  text-decoration: none;

}

.home {
  cursor: pointer;
  
}


.banner {
  background-image: url(../public/Images/hero.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  
}

.leftside  {
  padding: 100px 0px 100px 0px;
  text-align: justify;
}

.leftside b {color: #26acbb;}

h1.solution2 {
    color: #26acbb;
    font-size: 50px;
}

.solution {
  color: aliceblue;
  font-size: 50px;  
}

.finddoctor {
  border-radius: 20px;
  padding: 20px 0px;
}

.finddoctor button {
  padding: 11px 22px;
  border-radius: 20px;
  font-weight: 600;
}

.finddoctor button:nth-child(2) {
  margin-left: 15px;
  background-color: transparent;
  
}
@media(min-width: 751px){
  .trigger {
    display: none;
}

}


 @media only screen and (max-width: 750px){ 
  .header{
    justify-content: space-between;
    padding: 12px 15px;
    position: relative;
    align-items: center;
  }
  .logoleft {
    padding: 10px 0px 10px 0px;
  }
 .menuright {
    width: 100%;
    position: absolute;
    top: 83px;
    background-color: #fffffff7;
    left: 0px;
    right: 0px;
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    transition: all .3s;
  }
  .menuright.menuopen {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
}
  .menuright ul {
    flex-direction: column;
    text-align: center;
  }
  .trigger img {
    max-width: 30px;
  }

 
  .leftside  {
    padding: 0px 20px 0px 20px;
    text-align: justify;
  }
  
  .leftside b {
    font-size: 13px;
  }
  
  h1.solution2 {
      font-size: 20px;
  }
  
  .solution {
    font-size: 20px;  
  }
  .main_container {
    flex-direction: column;
    width: 100%;
  }
  .finddoctor button {
    font-size: 5px;
  }
}
